.wu {
  display: inline-block;
  background-repeat: no-repeat;
  padding: 0;
  margin: 0;
  /*  Custom sizing */
  width: var(--size);
  height: var(--size);
}

.wu-16 {
  width: 16px;
  height: 16px;
}

.wu-32 {
  width: 32px;
  height: 32px;
}

.wu-64 {
  width: 64px;
  height: 64px;
}

.wu-128 {
  width: 128px;
  height: 128px;
}

.wu-256 {
  width: 256px;
  height: 256px;
}

